.main {
    &__wrap {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        & > :not(only):not(:last-child) {
            margin-bottom: 20px;
        }

        .article {
            h1 {
                font-size: 40px;
            }
            p {
                font-size: 18px;
            }
        }
        .aside {
            width: 100%;
            overflow: hidden;
        }
    }
}