.bookform {
    position: relative;
    width: calc(100% + 30px);
    transform: translateX(-15px);


    &:before {
        bottom: 0;
        height: 40px;
    }

    &:after, &:before {
        content: "";
        position: absolute;
        z-index: 1;
        left: 50%;
        width: calc(100% + 2px);
        transform: translateX(-50%);
        background-color: var(--background-color);
    }
}